* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.navbar {
  position: sticky;
  top: 0;
  background: var(--bg);
  width: 100%;
  height: 10vh;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.navbar-logo {
  flex: 1;
  padding-left: 5rem;
  height: inherit;
  display: flex;
  align-items: center;
}
@media (max-width: 599px) {
  .navbar-logo {
    padding-left: 2rem;
  }
}
.navbar-links {
  transition: all 0.3s ease-in-out;
  flex: 1;
  padding-right: 5rem;
  height: inherit;
}
.navbar-links-list {
  height: inherit;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.navbar-links-list a:hover {
  color: var(--secondary-colour);
}

.burger {
  height: inherit;
  width: 2rem;
  margin-right: 5rem;
  display: flex;
  align-items: center;
}
@media (max-width: 599px) {
  .burger {
    margin-right: 2rem;
  }
}
.burger-container {
  height: 2rem;
  width: inherit;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  cursor: pointer;
}
.burger-container span {
  width: 2rem;
  height: 2px;
  background: var(--primary-colour);
  transform-origin: left;
  transition: all 0.3s ease-in-out;
}

html {
  font-family: "Montserrat", sans-serif;
}

h1 {
  font-size: 2rem;
}

h2 {
  font-size: 3.5rem;
}

a {
  text-decoration: none;
  font-size: 1.5rem;
  color: var(--primary-colour);
  font-weight: 700;
  transition: all 0.3s ease-in-out;
}
@media (max-width: 599px) {
  a {
    font-size: 1.2rem;
  }
}

li {
  list-style: none;
}

:root {
  --bg: #fff;
  --primary-colour: #29b58a;
  --secondary-colour: #b52954;
}

@media (max-width: 1023px) {
  .hide-for-mobile {
    position: absolute;
    top: 100%;
    right: 0;
    width: 30vw;
    height: 50vh;
    padding: 0;
    visibility: hidden;
    opacity: 0;
    z-index: -1;
  }
}
@media (max-width: 1023px) and (max-width: 599px) {
  .hide-for-mobile {
    width: 100%;
    height: 60vh;
  }
}
@media (max-width: 1023px) {
  .hide-for-mobile ul {
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
  }
}

@media (min-width: 1024px) {
  .hide-for-desktop {
    display: none;
  }
}

.burger-animation span:nth-child(1) {
  transform: rotatez(45deg);
  background: var(--secondary-colour);
}
.burger-animation span:nth-child(2) {
  transform: translatex(200%);
  opacity: 0;
}
.burger-animation span:nth-child(3) {
  transform: rotatez(-45deg);
  background: var(--secondary-colour);
}

.show-menu {
  z-index: 999;
  visibility: visible;
  opacity: 1;
  box-shadow: 10px 30px 20px rgba(0, 0, 0, 0.6);
}

.overflow {
  overflow: hidden;
}